@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap");
h1,
h2,
h3,
h4 {
  line-height: 1.4;
}

p,
blockquote {
  font-size: 0.9375rem;
  line-height: 1.6;
  font-weight: 500;
}

a,
a:visited,
a:active {
  text-decoration: none;
  background-color: transparent;
}

ul {
  list-style-type: none;
}

table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  text-align: left;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --Red: 0 78% 62%;
  --Cyan: 180 62% 55%;
  --Orange: 34 97% 64%;
  --Blue: 212 86% 64%;
  --selection-bg: 277 100% 50%;
  --Very-Dark-Blue: 234 12% 34%;
  --Grayish-Blue: 229 6% 66%;
  --Very-Light-Gray: 0 0% 98%;
  --white: 0 0% 100%;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}
.sr-only {
  position: absolute !important;
  margin: -1px !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  white-space: nowrap !important;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

::selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

::-moz-selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: "Poppins", sans-serif;
}

body {
  display: flex;
  justify-content: center;
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  background-color: hsl(var(--Very-Light-Gray));
}

.btn {
  display: inline-block;
  border: none;
  margin: 0;
  padding: 1rem 2rem;
  text-decoration: none;
  font-family: sans-serif;
  font-size: 1rem;
  line-height: 1;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.btn:hover,
.btn:focus {
  background: #0053ba;
}

.btn:focus {
  outline: none;
  outline-offset: -4px;
}

.btn:active {
  transform: scale(0.99);
}

.container {
  padding: 3.125rem;
}

.hero {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 0 auto;
}

.hero-title,
.hero-subtitle {
  font-size: 2.1875rem;
  color: hsl(var(--Very-Dark-Blue));
}

.hero-title {
  font-weight: 200;
}

.hero-subtitle {
  font-weight: 600;
}

.hero-detail {
  margin: 0.9375rem 0 2.5rem 0;
  color: hsl(var(--Grayish-Blue));
}

.cards-container {
  display: grid;
  justify-content: center;
  justify-items: center;
  align-items: center;
  grid-gap: 1.875rem;
  max-width: 56.25rem;
  justify-items: stretch;
}
@media (min-width: 62.5em) {
  .cards-container {
    max-width: 68.75rem;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
  }
}

.card {
  position: relative;
  background-color: hsl(var(--white));
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 0.5rem;
  padding: 2.1875rem;
}
.card::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  border-radius: 0.5rem 0.5rem 0 0;
}

.card-middle-wrapper {
  display: flex;
  flex-direction: column;
  gap: 1.875rem;
}

.card-1::before {
  background-image: linear-gradient(120.6345736374deg, rgb(180, 239, 243) 5.4125%, rgb(78, 240, 250) 96.9125%);
}

.card-2::before {
  background-image: linear-gradient(120.6345736374deg, rgb(245, 167, 128) 5.4125%, rgb(241, 43, 92) 96.9125%);
}

.card-3::before {
  background-image: linear-gradient(120.6345736374deg, rgb(247, 238, 147) 5.4125%, rgb(234, 220, 34) 96.9125%);
}

.card-4::before {
  background-image: linear-gradient(120.6345736374deg, rgb(237, 149, 255) 5.4125%, rgb(159, 1, 234) 96.9125%);
}

.card-title {
  color: hsl(var(--Very-Dark-Blue));
  font-size: 1.1875rem;
  font-weight: 600;
}

.card-detail {
  color: hsl(var(--Grayish-Blue));
  font-size: 0.8125rem;
  font-weight: 400;
}

.card-figure {
  float: right;
  padding-top: 2.5rem;
}